.tooltip {
  @apply ml-2 opacity-40 w-4 h-4 inline-flex relative transition-opacity items-center;
  background: url("/assets/images/info.svg") center center no-repeat;
}

.tooltip:before {
  content: attr(data-text); /* here's the magic */
  width: 240px;

  @apply absolute top-0 left-1/2 z-50 bg-gray-950 text-white text-sm opacity-0 py-2 px-3 rounded ml-2 pointer-events-none;
  transform: translate(-50%, calc(-100% - 10px));
}

.tooltip:after {
  content: "";
  @apply absolute left-1.5 -ml-1.5 top-0 transform -translate-y-1/2 opacity-0;
 
  /* the arrow */
  border: 10px solid #000;
  border-color: black transparent transparent transparent;
}

.tooltip:hover,
.tooltip:hover:before,
.tooltip:hover:after {
  @apply opacity-100;
}
