.faq {
  @apply border-b border-gray-200 px-4 lg:px-0;
}

.faq a {
  @apply underline;
}

.faq a:hover {
  @apply font-semibold;
}

.faq-question {
  @apply text-gray-800 pt-8 pb-6 pl-8 cursor-pointer relative text-lg font-bold list-none;
}

/* Safari compatibility */
.faq-question::-webkit-details-marker {
	display: none; 
}

.faq-question::before {
  @apply absolute left-0 top-8 w-6 h-6;

  content: " ";
  background: url(/assets/images/chevron.svg) center center no-repeat;
  transition: transform ease-in-out 0.125s;
}

.faq-answer {
  @apply text-gray-800 px-8 pb-10;
}

.faq[open] > .faq-question::before {
  transform: rotate(90deg);
}