.matrix-cell {
  @apply text-gray-800 px-4 py-3 text-center;
}

.matrix-sticky {
  @apply border-b border-gray-200 text-xl font-bold sticky top-0 bg-white;
}

.matrix-row-values {
  @apply rounded-lg; 
}

.matrix-row-values .matrix-cell {
  @apply text-gray-950 border-b border-gray-200 align-middle;  
}

.matrix-row-values:hover .matrix-cell {
  @apply  bg-gray-100; 
}
