.button {
  @apply block bg-transparent cursor-pointer h-11 text-center rounded flex justify-center items-center text-sm tracking-wider transition;
}

.button-outline {
  @apply border border-gray-950 bg-white;
}

.button-outline:hover {
  @apply bg-gray-100;
}

.button-primary {
  @apply bg-primary text-white font-semibold;
}

.button-primary:hover {
  @apply bg-primary-hover;
}

.button-disabled {
  @apply border border-gray-950 bg-white cursor-default;
}

.button-transparent {
  @apply bg-transparent text-gray-200;
}

.button-transparent:hover {
  @apply text-white;
}

.button-selected.button-transparent {
  @apply bg-gray-600 text-white;
}
